@import '../../../common/styles/colors.module';

.meal {
  border-radius: 10px;
  width: 24%;
  display: inline-block;
  position: relative;
  text-align: center;
  min-height: 60vh;
  margin: 0  1%;
  box-shadow: 0 0 5px 1px rgba(0,0,0,1);
  background-color: $white;
}

.heading {
  text-transform: capitalize;
  margin: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: $light;
  padding: 10px 0;
  color: $grey;
}

.add-recipe-button {
  font-size: 0.8rem;
  font-weight: 600;
  position: absolute;
  bottom: 20px;
  right: 20px;
}

.delete-btn {
  display: inline-flex;
  margin: 0 5px;
  padding: 0;
}

.delete-btn:hover ~ .recipe {
  text-decoration: line-through;
  transition: all 0.5s;
}

.recipe-container {
  list-style: none;
  display: flex;
  margin: 0 auto;
  border-bottom: 1px solid $neutral;
  width: 90%;
}

.recipe {
  color: $black;
  padding: 0;
  font-size: 1.1rem;
  background: transparent;
}

.recipes {
  text-align: left;
  margin-left: 5px;
  margin-top: 10px;
}
