@import '../../common/styles/colors.module';

.timer {
  margin: 150px auto;
  text-align: center;
  background-color: $white;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 1);
  width: 70%;
}

.inputs {
  display: block;
}

.buttons {
  display: block;
  margin: 20px auto;
}

.start-button {
  width: 80px;
}

.stop-button {
  width: 80px;
  background: $negative;
  color: $white;
  border: 1px solid $negative;
  padding: 5px 10px;
  font-size: 1rem;
}

.stop-button:hover {
  color: $negative;
  background: $white;
  transform: none;
}

.reset-button {
  margin-left: 10px;
  width: 80px;
  border: 1px solid $navy-blue;
}

.reset-button:hover {
  transform: none;
  background: $white;
  color: $navy-blue;
}
