@import '../../../common/styles/colors.module';

.heading {
  margin: 0;
  text-transform: uppercase;
}

.container {
  display: inline-block;
  margin: 10px 50px 30px;
}

.time-unit {
  font-size: 5rem;
  background: $light;
  color: $navy-blue;
  padding: 20px;
  border-radius: 10px;
  width: 100px;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.3);
}

