@import '../../styles/colors.module';

.display-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width:100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 1;
}

.modal-main {
  position: fixed;
  background: white;
  padding: 30px;
  border-radius: 10px;
  width: 70%;
  height: 75%;
  box-shadow: 0 0 5px 1px rgba(0,0,0,1);
}

.close-button {
  position: absolute;
  right: 15px;
  top: 10px;
}
