@import '../../../common/styles/colors.module';

.day {
  display: inline-block;
  width: 22%;
  min-height: 40vh;
  margin: 1% 1%;
  box-shadow: 0 0 5px 1px rgba(0,0,0,1);
  border-radius: 10px;
  background: $white;
}

.meals {
  padding: 10px;
}

.day-heading {
  text-align: center;
  padding: 5px;
  margin: 0;
  background-color: $light;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: $grey;
}
