@import '../../../../common/styles/colors.module';

.recipe-name {
  font: {
    size: 1.7rem;
    family: 'Open Sans', 'Verdana', 'sans-serif';
    weight: bold;
  }
  width: 70%;
  border: none;
  &:focus {
    outline: 0;
  }
  display: block;
}

.ingredients-heading {
  text-align: left;
  margin-bottom: 0;
}

.add-ingredient {
  display: flex;
}

.ingredient-field {
  border: 1px solid;
  border-color: $neutral;
  border-radius: 5px;
  width: 270px;
  &:focus {
    outline: 0
  }
  font: {
    size: 1rem;
    family: 'Open Sans', 'Verdana', 'sans-serif';
  }
  display: flex;
}

.add-ingredient-button {
  display: flex;
  font-size: 0.7rem;
  margin-right: 10px;
  height: 100%;
}

.delete-btn {
  display: inline-flex;
  margin-right: 5px;
}

.ingredients-list {
  text-align:left;
  padding-left: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 100px;
  width: fit-content;
}

.ingredient {
  margin-right: 50px;
  list-style: none;
  display: flex;
}

.notes-heading {
  text-align: left;
}

.notes {
  display: block;
  border: 1px solid $neutral;
  border-radius: 5px;
  width: 98%;
  height: 150px;
  &:focus {
    outline: 0;
  }
  font: {
    size: 1rem;
    family: 'Open Sans', 'Verdana', 'sans-serif';
  }
}

.save-button {
  display: inline-block;
  position: absolute;
  right: 120px;
  bottom: 30px;
}

.clear-button {
  background: $negative;
  padding: 5px 10px;
  display: inline-block;
  position: absolute;
  right: 50px;
  bottom: 30px;
  font-size: 1rem;
  border: 1px solid $negative;
  color: $white;
}

.clear-button:hover {
  background: $white;
  color: $negative;
  transform: none;
}

