@import '../../styles/mixins.module';
@import '../../styles/colors.module';

.button {
  @include button
}

.button--positive {
  color: $white;
  background: $positive;
  border: 1px solid transparent;
  border-radius: 5px;
  padding: 5px 10px;
}

.button--positive:hover {
  color: $positive;;
  background: $white;
  border: 1px solid $positive;
  transition: all 0.5s;
}

.button--negative {
  color: $neutral;
  background: transparent;
  padding: 0;
  font-size: 0.8rem;
}

.button--negative:hover {
  color: $negative;
  transform: scale(1.5);
  transition: all 0.5s;
}

.button--neutral {
  color: $white;
  background: $navy-blue;
}

.button--neutral:hover {
  transform: scale(1.1);
  transition: all 0.5s;
}
