@import '../../../common/styles/colors.module';

.heading {
  margin: 0;
  text-transform: uppercase;
}

.container {
  display: inline-block;
  margin: 10px 50px 30px;
  text-align: center;
}

.time-input {
  font: {
    size: 5rem;
    family: "Open Sans", "Verdana", "sans-serif";
  }
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.3);
  color: $navy-blue;
  width: 100px;
  border: none;
  text-align: center;
  background: $light;
  &:focus {
    outline: 0;
  }
}

.time-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
