@import '../../styles/colors.module';

.container {
  background: $grey;
  height: 80px;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  opacity: 0.9;
  z-index: 1;
}

.logo {
  color: $white;
  margin: auto 20px;
  font-family: 'Englebert', 'Verdana', 'sans-serif';
  font-size: 2.5rem;
}

.logo-span {
  color: #4bb49a;
}

.navbar {
  margin: auto 0;
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.nav-button {
  border: 1px solid transparent;
  border-radius: 4px;
  background: $grey;
  color: $white;
  padding: 12px 10px 7px;
  margin: 0 7px;
  font-size: 1.5rem;
  font-family: 'Englebert', 'Verdana', 'sans-serif';
  cursor: pointer;
  &:focus {
    outline: 0;
  }
}

.nav-button:hover {
  border: 1px solid $white;
  transform: scale(1.05);
  transition: all 0.5s;
}

.active-link {
  color: $grey;
  background: $white;
  transition: all 0.5s;
  transform: scale(1.05);
}
