@import '../../../../common/styles/colors.module';

.meal {
  margin-bottom: 15px;
  position: relative;
}

.header-container {
  display: flex;
  align-items: center;
  position: relative;
}

.meal-heading {
  text-transform: capitalize;
  margin: 0;
  margin-right: 5px;
}

.delete-button {
  margin-right: 5px;
}

.dish {
  display: flex;
  align-items: center;
  border-bottom: 1px solid $neutral;
}

.select {
  -webkit-appearance: none;
  background: $positive;
  border: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  color: $white;
  font: {
    family: "Open Sans", "Verdana", "sans-serif";
    size: 0.8rem;
  }
  text-align-last: center;
  &:focus {
    outline: 0;
  }
  &:hover {
    cursor: pointer;
    background: $white;
    color: $positive;
    border: 1px solid $positive;
  }
}

.hint {
  font-size: 0.7rem;
  color: $neutral;
}

