@import '../../../common/styles/colors.module';

.daily-list {
  display: inline-block;
  width: 20%;
  margin: 0.5%;
  min-height: 40vh;
  margin-top: 20px;
  box-shadow: 0 0 5px 1px rgba(0,0,0,1);
  border-radius: 10px;
  background: $white;
}

.day-heading {
  text-align: center;
  margin: 0;
  background: $light;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  padding: 5px;
  color: $grey;
}

.ingredient-container {
  margin: 7px 15px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid $neutral;
  width: 90%;
}

.ingredient-checkbox {
  width: 20px;
  height: 20px;
  padding: 0;
  border-radius: 50%;
  border: 1px solid $positive;
  transition: all 300ms;
  margin-right: 5px;
  -webkit-appearance: none;
  display: flex;
  justify-content: center;
  align-items: center;
  &:focus {
    outline: 0;
  }
  cursor: pointer;
}

.ingredient-checkbox:checked {
  background-color: $positive;
  background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5.414 11L4 12.414l5.414 5.414L20.828 6.414 19.414 5l-10 10z' fill='%23fff' fill-rule='nonzero'/%3E%3C/svg%3E ");
  background-position: 1px 2px;
}

.ingredient-checkbox:checked ~ span{
  color: $neutral;
  text-decoration: line-through;
}

.delete-button {
  padding: 1px 4px;
  font-size: 0.7rem;
  margin-left: 5px;
}

.hint {
  font-size: 0.7rem;
  text-align: center;
  color: $neutral;
  margin-top: 10px;
}
